import React from 'react';


const Participant = (props) =>{

        
    
        if(props.details.deleted){
            return(
                null
            );
        }
        
        
        let classes = ['addbutton'];
        if(props.details.selected){
            classes.push("added");
        }
        let classes2 = [];
        classes2.push("row-".concat(props.id));
        
        return(
            
            <tr   className ={classes2.join(" ")}>
                <th>{props.details.name}</th>
                <th>{props.details.roll}</th>
                <th>{props.details.Subgroup}</th>
                <th className={classes.join(' ')} onClick={() => props.additem(props.id)}>{props.details.selected ? <p>Added</p> : <p>Add</p>}</th>
                <th className= "deregbutton" onClick={() => props.deleteitem(props.id)}>DeReg</th>
            </tr>

            
        
        );
}

export default Participant;