import React from 'react';
import NavigationBar from '../NavigationBar/NavigationBar';
import Logo from '../Logo/Logo';

import Subject from './Subject';

class Subjects extends React.Component{

    state = {
        number : {
            CS10001:0,
            CS21003:0,
            CS19001:0,
            CS20006:0,
            CS31003:0,
        },

        totalnumber : {
            CS10001:6,
            CS21003:6,
            CS19001:6,
            CS20006:6,
            CS31003:6,
        }

    };

    componentWillMount(){
        const localstorageref = localStorage.getItem("student");
        if(localstorageref){
            let subjectstudent= {...JSON.parse(localstorageref)};
            var copynumber = {...this.state.number};
            let copytotalnumber = {...this.state.totalnumber};
            console.log(subjectstudent);
            let count =0;
            let count1 = 0;
            for(let i=0 ; i <6 ; i++){
                if(subjectstudent.CS10001[i].selected && !subjectstudent.CS10001[i].deleted){
                    count++;
                }
                if(!subjectstudent.CS10001[i].deleted){
                    count1++;
                }
            }
            copynumber.CS10001 = count;
            copytotalnumber.CS10001 = count1;


            count=0;
            count1=0;
            
            for(let i=0 ; i <6 ; i++){
                if(subjectstudent.CS21003[i].selected && !subjectstudent.CS21003[i].deleted){
                    count++;
                }
                if(!subjectstudent.CS21003[i].deleted){
                    count1++;
                }
                
            }
            copynumber.CS21003 = count;
            copytotalnumber.CS21003 = count1;

            count=0;
            count1=0;
            for(let i=0 ; i <6 ; i++){
                if(subjectstudent.CS19001[i].selected && !subjectstudent.CS19001[i].deleted){
                    count++;
                }
                if(!subjectstudent.CS19001[i].deleted){
                    count1++;
                }
            }
            copynumber.CS19001 = count;
            copytotalnumber.CS19001 = count1;

            count=0;
            count1=0;
            for(let i=0 ; i <6 ; i++){
                if(subjectstudent.CS20006[i].selected && !subjectstudent.CS20006[i].deleted){
                    count++;
                }
                if(!subjectstudent.CS20006[i].deleted){
                    count1++;
                }

            }
            copynumber.CS20006 = count;
            copytotalnumber.CS20006 = count1;

            count=0;
            count1=0;
            for(let i=0 ; i <6 ; i++){
                if(subjectstudent.CS31003[i].selected && !subjectstudent.CS31003[i].deleted){
                    count++;
                }
                if(!subjectstudent.CS31003[i].deleted){
                    count1++;
                }
            }
            copynumber.CS31003 = count;
            copytotalnumber.CS31003=count1;

            this.setState({number:copynumber, totalnumber:copytotalnumber});
            
        }

        

    }
    

    homeclick = () =>{
        this.props.history.push('/');
    }
    participantsclick = (subjectname) =>{
        this.props.history.push('/participants/'.concat(subjectname));
    }
    
    render(){
        return(
            <React.Fragment>
                <body>
                 
                <NavigationBar participantsclick={() => this.participantsclick("CS10001")} homeclick={this.homeclick} page="Subjects"/>
                <div className="subjectspage">
                    <Logo />
                    <div className="subjectcards">
                        <Subject index="1" participantclick = { this.participantsclick } title="CS10001" fulltitle="PDS" semester="Autumn" credit="3-0-0" prof="K. Sharma"  number= {this.state.number.CS10001} totalnumber= {this.state.totalnumber.CS10001}/>
                        <Subject index="2" participantclick = { this.participantsclick } title="CS21003" fulltitle="Algorithm-I" semester="Spring" credit="3-1-0" prof="P. Patra"  number= {this.state.number.CS21003} totalnumber= {this.state.totalnumber.CS21003}/>
                        <Subject index="3" participantclick = { this.participantsclick } title="CS19001" fulltitle="PDS Lab" semester="Autumn" credit="0-1-3" prof="R. Iyer"  number= {this.state.number.CS19001} totalnumber= {this.state.totalnumber.CS19001}/>
                        <Subject index="4" participantclick = { this.participantsclick } title="CS20006" fulltitle="Software" semester="Autumn" credit="3-0-0" prof="A. Somnath"  number= {this.state.number.CS20006} totalnumber= {this.state.totalnumber.CS20006}/>
                        <Subject index="5" participantclick = { this.participantsclick } title="CS31003" fulltitle="Compilers" semester="Spring" credit="3-0-0" prof="S. Jain"  number= {this.state.number.CS31003} totalnumber= {this.state.totalnumber.CS31003}/>
                        
                    </div>  
                </div>  
                </body>
            </React.Fragment>
        );
    }
    
}
export default Subjects;