import React from 'react';
import NavigationItems from './NavigationItems/NavigationItems';
import Backdrop from '../backdrop';

const NavigationBar = (props) =>{
    
    

    if(props.page === "Subjects"){
        return(

            <React.Fragment>

            <div id="menuToggle">
                    <input type="checkbox" />

                    <span></span>
                    <span></span>
                    <span></span>
            <Backdrop />
            <aside className="sidebar">
            <nav className="nav">
            <ul>
                <li onClick={props.homeclick}><NavigationItems itemname="Home" id ="Home"/></li>
                <li  className="active"><NavigationItems itemname="Subjects" id="Subjects"/></li>
                <li onClick={props.participantsclick}><NavigationItems itemname="Participants" id="Participants"/></li>
                
            </ul>
            </nav>
            </aside>
            </div>
            </React.Fragment>
        );
    }
    else if(props.page === "Home"){
        return(
            <React.Fragment>

            <div id="menuToggle">
                    <input type="checkbox" />

                    <span></span>
                    <span></span>
                    <span></span>
            <Backdrop />
            <aside class="sidebar">
            <nav className="nav">
            <ul>
                <li  className="active"><NavigationItems itemname="Home" id ="Home"/></li>
                <li onClick={props.subjectclick}><NavigationItems itemname="Subjects" id="Subjects"/></li>
                <li onClick={props.participantsclick}><NavigationItems itemname="Participants" id="Participants"/></li>
                
            </ul>
            </nav>
            </aside>
            </div>
            </React.Fragment>
        );
    }
    else if(props.page === "Participants"){
        return(

            <React.Fragment>

            <div id="menuToggle">
                    <input type="checkbox" />

                    <span></span>
                    <span></span>
                    <span></span>
            <Backdrop />
            <aside  className="sidebar">
            <nav className="nav">
                

                    <ul>
                        <li onClick={props.homeclick} ><NavigationItems itemname="Home" id ="Home"/></li>
                        <li onClick={props.subjectclick} ><NavigationItems itemname="Subjects" id="Subjects"/></li>
                        <li className="active"><NavigationItems itemname="Participants" id="Participants"/></li>
                        
                    </ul>
                
            </nav>
            </aside>
            </div>
            </React.Fragment>
        );
    }

    

    

}

export default NavigationBar;