import React from 'react';



const Subject = (props) =>{
    var size = (props.number/props.totalnumber)*100;
    if(props.number===0){
        size=4;
    }
    var temp = size.toString();
    var result = temp.concat("%");
    const styles ={
            width: result,
    }

    let classes =['subject-card'];

    classes.push('subject-card-'.concat(props.index));

        return(
            <div onClick={() =>{props.participantclick(props.title)} }  className={classes.join(" ")}>
                <h3 className="subject-title">{props.title}<br></br>
                {props.fulltitle}
                </h3>

                <div className="subject-bar">
                <div className="subject-emptybar"></div>
                <div className="subject-filledbar" style={styles}></div>
                <h2>Participants: {props.number} out of {props.totalnumber}</h2>
                </div>
                <div className="subject-description">
                    <h1>Semester: {props.semester} </h1>
                    <h1 >Credits: {props.credit} </h1>
                    <h1>Session: 2019-20 </h1>
                    <h1>Professor: {props.prof} </h1>
                </div>
            </div>
        )
}

export default Subject;