import React from 'react';
import NavigationBar from '../NavigationBar/NavigationBar';
import Logo from '../Logo/Logo';


class Home extends React.Component{

    subjectclick = () =>{
        this.props.history.push('/subjects');
    }
    participantsclick = () =>{
        this.props.history.push('/participants/CS10001');
    }
    render(){
        return(
            <React.Fragment>
                <body>
                
                <NavigationBar participantsclick={this.participantsclick} subjectclick = {this.subjectclick} page = "Home"/>
                <div className="homepage">
                    <Logo />
                    <div className="title"><h1>The <b className="blue">T</b>otal <b className="red">A</b>ccess App</h1></div>
                    <div className="left">
                        <h1>Welcome to the TA App</h1>
                        <p>
                            Your One Stop destination for all the things you ever need.
                            All the subjects that you teach can be found here. 
                            </p>
                        
                            <p>
                            Here's a list of items that you con access in one click:-

                        </p>
                        <ul>
                            <li>Select students that you need to monitor</li>
                            <li>Give them the disired marks at a go</li>
                            <li>Kick them out, if you want</li>
                            <li>Get all your subjects monitored</li>
                        </ul>
                        
                    </div>
                    <div className="right">
                        <h1>All New possibilities</h1>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscin ostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
                    </div>
                    
                </div>
                    
                </body>
            </React.Fragment>
        );
    }
    
}
export default Home;